// RegisterTeacher_HeadTeacher
import React, { FC, ReactElement, useState } from "react";
import { MdCheckCircle } from "react-icons/md";
import { connect } from "react-redux";
import { Basic } from "../../actions";
import { Auth } from "../../actions/auth.action";
import { AssignStuffComponent } from "../../components/AssignStuffComponent/AssignStuffComponent";
import Button from "../../components/Buttons/Button";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Heading from "../../components/Heading/Heading";
import { StoreState } from "../../reducers";

type RegisterStaffProps = {
  auth: Auth;
  basic: Basic;
};

const _RegisterStaff: FC<RegisterStaffProps> = ({
  auth,
  basic,
}): ReactElement => {
  const [staff_created, setStaffCreated] = useState(false);

  if (!auth.user)
    return <div className="p-4 text-center">No profile found!</div>;

  if (staff_created)
    return (
      <DashboardContainer className="p-3 animate__animated animate__zoomIn animate">
        <div className="py-4 text-center">
          <MdCheckCircle className="mx-auto text-8xl text-green-500" />
          <h1 className="text-3xl mt-2 ">The candidate has been created</h1>
        </div>
        <br />

        <div className="text-center mx-auto">
          <Button theme="accent-light" onClick={() => setStaffCreated(false)}>
            Add another administration user
          </Button>
        </div>
      </DashboardContainer>
    );

  return (
    <>
      <DashboardContainer className="p-3">
        <div className="px-3">
          <Heading size="lg">Administration staff registration</Heading>
          <p className="opacity-50">
            This form is not form the <b>Teacher</b> and <b>Head-teachers</b>
          </p>
        </div>
      </DashboardContainer>

      <AssignStuffComponent
        district_id={""}
        // school={{
        //   school_id: auth.school.school_code,
        //   school_name: auth.school.school_name,
        // }}
        select_district_active={true}
        assignType="REGISTER"
        button_title="Create administration user"
        exclude_roles={["1", "2"]}
        user_id={""}
        formAccess={{
          country_id: true,
          sector_code: false,
          stakeholder_id: true,
          role_id: true,
          district_code: true,
          school_code: false,
          qualification_id: false,
          academic_year_id: true,
          start_date_in_the_school: false,
          position_id: false,
          first_name: true,
          last_name: true,
          full_name: true,
          middle_name: true,
          nid: true,
          phone_numbers: true,
        }}
        required={{
          role_id: true,
          country_id: false,
          district_code: true,
          sector_code: true,
          school_code: false,
          qualification_id: false,
          academic_year_id: true,
          stakeholder_id: false,
          start_date_in_the_school: false,
          position_id: false,
          first_name: true,
          last_name: true,
          full_name: true,
          middle_name: false,
          nid: true,
          phone_numbers: true,
        }}
        success={(data) => {
          setStaffCreated(true);
        }}
      />
    </>
  );
};

const mapStateToProps = ({
  auth,
  basic,
}: StoreState): { auth: Auth; basic: Basic } => {
  return { auth: auth, basic: basic };
};

export const RegisterTeacher_HeadTeacher = connect(
  mapStateToProps,
  {}
)(_RegisterStaff);
